
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}
.center {
  text-align: center;
}
.left {
  text-align: left;
}
.right {
  text-align: right;
}

.new-song-form form {
  display: grid;
  grid-template-rows: 1fr 0.75fr 0.75fr 0.75fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-areas: 'header header header header header' '. title title title duration' '. artist artist artist .' '. . submit . .';
  border: 1px solid #f3fafc;
  border-radius: 8px;
  background-color: #edfbfc;
  padding: 10px 80px;
}
input,
button {
  display: block;
  min-width: 90%;
  font-size: 1rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  border: 1px solid #eee;
  border-radius: 8px;
  padding: 1rem 0;
  background-color: #98bcca;
  color: white;
}
input:focus, input:active,
  button:focus,
  button:active {
    color: #000;
}
input.duration,
  button.duration {
    background-color: rgba(152, 188, 202, 0.3);
}
input.empty,
  button.empty {
    color: white;
    border-color: #5f1c1c;
    background-color: #f5c6be;
}
button {
  display: block;
  margin: 1rem auto;
  min-width: 200px;
  background-color: #154f69;
  color: #fff;
}
::-webkit-input-placeholder {
  color: #132e3d;
}
.song-items-container {
  margin: 2em auto;
}
.song-items-container .song-item-container {
    display: grid;
    grid-template-rows: [preSlot] auto [songInfo] 40px [postSlot] auto;
    grid-template-columns: 80px 1fr 2fr 1fr 80px;
    grid-template-areas: 'pre pre pre pre pre' 'order artist title . duration' 'post post post post post';
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0;
}
.medium-width, .song-items-container {
  max-width: 800px;
}

hr[data-v-c12acf28] {
  border: none;
  border-bottom: 1px solid #eee;
}
